
import { defineComponent, reactive, toRefs, watch } from "vue";
import { getSearchHotDetail as getSearchHotDetailApi } from "@/api/search";
import { AxiosResponseProps } from "@/utils/request";
import { useRouter } from "vue-router";
const searchHistoryKey = "searchHistory";
export default defineComponent({
    emits: ["update:modelValue", "update:keyword", "unshiftKeyword"],
    props: {
        modelValue: {
            type: Boolean,
            required: true,
        },
        keyword: {
            type: String,
            required: true,
        },
    },
    setup(props, context) {
        const router = useRouter();
        const state = reactive({
            historyList: [],
            spreadList: false,
            hotSearchList: [],
            deleteHistoryModalVisible: false,
            isLoading: false,
        });
        const getHistory = () => {
            state.historyList = localStorage[searchHistoryKey] ? JSON.parse(localStorage[searchHistoryKey]) : [];
        };
        watch(
            () => props.modelValue,
            newV => {
                getHistory();
            },
            {
                immediate: true,
            }
        );
        const removeHistoryItem = (item, index) => {
            state.historyList.splice(index, 1);
            localStorage[searchHistoryKey] = JSON.stringify(state.historyList);
        };
        const getSearchHotDetail = () => {
            state.isLoading = true;
            getSearchHotDetailApi().then((res: AxiosResponseProps) => {
                state.isLoading = false;
                if (res.code === 200) {
                    state.hotSearchList = res.data;
                }
            });
        };
        getSearchHotDetail();
        const deleteHistory = () => {
            state.deleteHistoryModalVisible = true;
        };
        const confirmDeleteHistory = () => {
            state.deleteHistoryModalVisible = false;
            localStorage[searchHistoryKey] = "";
            getHistory();
        };
        const toSearchResultDetail = hotItem => {
            router.push({ path: "/container/searchResultDetail", query: { keyword: hotItem.searchWord as string } });
            context.emit("update:keyword", hotItem.searchWord);
            context.emit("unshiftKeyword", hotItem.searchWord);
            context.emit("update:modelValue", false);
        };
        return {
            ...toRefs(state),
            removeHistoryItem,
            deleteHistory,
            confirmDeleteHistory,
            toSearchResultDetail,
        };
    },
});
