
import { useStore } from "vuex";
import { computed, defineComponent, reactive, ref, toRefs, watch } from "vue";
import { dailySignin, getAccountInfo } from "@/api/login";
import { AxiosResponseProps } from "@/utils/request";
export default defineComponent({
    props: {
        visible: {
            type: Boolean,
            default: false,
        },
    },
    setup(props, { emit }) {
        const store = useStore();
        watch(
            () => props.visible,
            newV => {
                if (newV) {
                    store.dispatch("getUserDetail");
                }
            }
        );
        const state = reactive({
            userDetail: computed(() => store.state.userDetail),
            visible: computed({
                get: () => props.visible,
                set: newV => emit("update:visible", newV),
            }),
            commonToast: ref(null),
        });
        const handleLogout = () => {
            store.dispatch("logout");
        };
        const handlePcSign = () => {
            dailySignin().then((res: any) => {
                if (res.code === 200) {
                    store.dispatch("getUserDetail").then(() => {
                        state.commonToast.success(`签到成功，积分加${res.point}`);
                    })
                }
            });
        };
        return {
            ...toRefs(state),
            handleLogout,
            handlePcSign,
        };
    },
});
