
import { defineComponent, reactive, toRefs } from "vue";
export default defineComponent({
    setup() {
        const state = reactive({
            visible: false,
            icon: "success",
            duration: 1200,
            startCloseAni: false,
            tips: ""
        });
        const success = (tips: string, options?: { duration: number }) => {
            state.duration = options && options.duration && options.duration !== 0 ? options.duration : 1200;
            state.tips = tips;
            state.icon = "success";
            startAni();
        };
        const warn = (tips: string, options?: { duration: number }) => {
            state.duration = options && options.duration && options.duration !== 0 ? options.duration : 1200;
            state.tips = tips;
            state.icon = "warn";
            startAni();
        };
        const error = warn;
        let timer1 = null;
        let timer2 = null;
        const startAni = () => {
            if (timer1) {
                clearTimeout(timer1);
                timer1 = null;
            }
            if (timer2) {
                clearTimeout(timer2);
                timer2 = null;
            }
            state.visible = true;
            timer1 = setTimeout(() => {
                state.startCloseAni = true;
                timer2 = setTimeout(() => {
                    state.startCloseAni = false;
                    state.visible = false;
                }, 800);
            }, state.duration);
        };
        return {
            ...toRefs(state),
            success,
            warn,
            error
        };
    },
});
