
import { useRouter } from "vue-router";
import { computed, defineComponent, PropType, reactive, toRefs, ComputedRef } from "vue";
export interface NavsProps {
    title: string | ComputedRef<string>;
    key?: number;
    active?: boolean;
    path?: string;
}
export default defineComponent({
    props: {
        navs: {
            type: Array as PropType<NavsProps[]>,
            default: [],
        },
    },
    emits: ["update:navs"],
    setup(props, { emit }) {
        const router = useRouter();
        const state = reactive({
            navList: computed({
                get: () => props.navs,
                set: newV => emit("update:navs", newV),
            }),
        });
        //点击导航标题
        const clickNavItem = (nav, navIndex) => {
            state.navList = (state.navList as any[]).map((item, index) => {
                if (index == navIndex) {
                    return {
                        ...item,
                        active: true,
                    };
                } else {
                    return {
                        ...item,
                        active: false,
                    };
                }
            });
            if (nav.path) {
                router.push(nav.path);
            }
        };
        return {
            ...toRefs(state),
            clickNavItem,
        };
    },
});
